import * as React from "react";
import style from "./TheTeam.module.scss";
import {H2} from "../D_Common/H2/H2";
import src0 from "../../assets/png/the team/src0.png";
import src1 from "../../assets/png/the team/src1.png";
import src2 from "../../assets/png/the team/src2.png";
import src3 from "../../assets/png/the team/src3.png";

const team = [
    { name: "Matt Quadro", job: "Founder & Chairman", src: src0 },
    { name: "Matt Quadro", job: "Founder & Chairman", src: src1 },
    { name: "Matt Quadro", job: "Founder & Chairman", src: src2 },
    { name: "Matt Quadro", job: "Founder & Chairman", src: src3 },
]

export const TheTeam = () => {
    return (
        <section className={style.theTeam}>

            <H2 titles={["the", "team"]}/>

            <div className={style.content}>
                <p className={style.description} data-aos="flip-up">
                    Were a group of game industry <span>veterans</span> from some of the <span>top tech/game companies</span>, with experience of successfully launching global titles that have acquired <span>hundreds of millions</span> in downloads.
                </p>

                <div className={style.team}>
                    {
                        team.map(({name, job, src}, index) => (
                            <div className={style.item} key={index}>
                                <img src={src} alt="" data-aos="flip-up"/>

                                <div className={style.info}>
                                    <p data-aos="flip-up">{name}</p>
                                    <p data-aos="flip-up">{job}</p>
                                </div>
                            </div>
                        ))
                    }
                </div>
            </div>

        </section>
    )
}